import React, { createContext, useContext, useState } from 'react';

const JobContext = createContext();

export function useJobs() {
  return useContext(JobContext);
}

// Mock job data for development
const MOCK_JOBS = [
  {
    id: 1,
    name: "Downtown Paint Project",
    jobNumber: "DP-2024-001",
    location: "123 Main St, Seattle",
    startDate: new Date(2024, 2, 15),
    endDate: new Date(2024, 2, 20),
    status: "active",
    type: "paint",
    description: "External paint job for downtown office building",
    assignedEmployees: ["test@test.com"],
    documents: []
  }
];

export function JobProvider({ children }) {
  const [jobs, setJobs] = useState(MOCK_JOBS);

  const addDocument = (jobId, document) => {
    setJobs(jobs.map(job => {
      if (job.id === jobId) {
        return {
          ...job,
          documents: [...job.documents, { ...document, id: Date.now() }]
        };
      }
      return job;
    }));
  };

  const value = {
    jobs,
    setJobs,
    addDocument
  };

  return (
    <JobContext.Provider value={value}>
      {children}
    </JobContext.Provider>
  );
}